import React, { useState, useEffect } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Home from './components/Home';
import UserProfile from './components/UserProfile';
import LogIn from './Login';
import Debit from './components/Debit';
import Credit from './components/Credit';
import axios from 'axios';

function App() {
    const [user, setUser] = useState(
        {
            accountBalance: 14568.27,
            currentUser: {
                userName: 'joe_shmo',
                memberSince: '07/23/96',
            },
            debits: [],
            credits: [],
        }
    )

    async function fetchData() {
        const newUser = {...user};
        
        axios.get("https://moj-api.herokuapp.com/debits")
            .then(response => {
                newUser.debits = response.data;
            })
            .catch(err => console.log(err));

        axios.get("https://moj-api.herokuapp.com/credits")
            .then(response => {
                newUser.credits = response.data;
            })
            .catch(err => console.log(err));

        setUser(newUser);
    }
    
    useEffect(() => {
        fetchData();
    }, [])
    
    function mockLogIn(logInInfo) {
        const newUser = user.currentUser;
        newUser.userName = logInInfo.userName
        setUser({ currentUser: newUser })
    }

    function addDebit(deb) {
        setUser(...user.debits, deb);
    }

    function addCredit(cred) {
        const newUser = {...user};
        newUser.credits = newUser.credits.concat(cred);
        setUser(newUser);
    }

    function updateBal() {
        const balance = 0;
        user.credits.map(cred => (
            balance -= cred.amount
        ))
        user.debits.map(deb => (
            balance += deb.amount
        ))
        const newUser = {...user};
        newUser.accountBalance = balance;
        setUser(newUser)
    }

    return (
        <BrowserRouter>
            <Switch>
                <Route exact path="/login">
                    <LogIn mockLogIn={mockLogIn} />
                </Route>
                <Route exact path="/" component={Home}>
                    <Home user={user} />
                </Route>
                <Route exact path="/userProfile">
                    <UserProfile user={user.currentUser} />
                </Route>
                <Route exact path="/Debit"> 
                    <Debit debits={user.debits} />
                </Route>
                <Route exact path="/Credit">
                    <Credit addCredit={addCredit} credits={user.credits} />
                </Route>
            </Switch>
        </BrowserRouter>
    );

}

export default App;